.footer {
	background-color: #575050;
	width: 100%;
	text-align: left;
	font-family: sans-serif;
	font-weight: bold;
	font-size: 16px;
	padding: 20px;
	margin-top: 40px;
  }
  
  .footer .footer-left,
  .footer .footer-center,
  .footer .footer-right {
	display: inline-block;
	vertical-align: top;
  }
  
  
  /* footer left*/
  
  .footer .footer-left {
	width: 33%;
	padding-right: 15px;
  }
  
  .footer .about {
	line-height: 20px;
	color: #ffffff;
	font-size: 13px;
	font-weight: normal;
	margin: 0;
  }
  
  .footer .about span {
	display: block;
	color: #ffffff;
	font-size: 14px;
	font-weight: bold;
	margin-bottom: 20px;
  }
  
  .footer .icons {
	margin-top: 25px;
  }
  
  .footer .icons a {
	display: inline-block;
	width: 35px;
	height: 35px;
	cursor: pointer;
	background-color: #33383b;
	border-radius: 2px;
	font-size: 20px;
	color: #ffffff;
	text-align: center;
	line-height: 35px;
	margin-right: 3px;
	margin-bottom: 5px;
  }
  
  
  /* footer center*/
  
  .footer .footer-center {
	width: 30%;
  }
  
  .footer .footer-center i {
	background-color: #33383b;
	color: #ffffff;
	font-size: 25px;
	width: 38px;
	height: 38px;
	border-radius: 50%;
	text-align: center;
	line-height: 42px;
	margin: 10px 15px;
	vertical-align: middle;
  }
  
  .footer .footer-center i.fa-envelope {
	font-size: 17px;
	line-height: 38px;
  }
  
  .footer .footer-center p {
	display: inline-block;
	color: #ffffff;
	vertical-align: middle;
	margin: 0;
  }
  
  .footer .footer-center p span {
	display: block;
	font-weight: normal;
	font-size: 14px;
	line-height: 2;
  }
  
  .footer .footer-center p a {
	color: #3dacf6;
	text-decoration: none;
  }
  
  
  /* footer right*/
  
  .footer .footer-right {
	width: 35%;
  }
  
  .footer h2 {
	color: #ffffff;
	font-size: 36px;
	font-weight: normal;
	margin: 0;
  }
  
  .footer h2 span {
	color: #3baffc;
  }
  
  .footer .menu {
	color: #ffffff;
	margin: 20px 0 12px;
	padding: 0;
  }
  
  .footer .menu a {
	display: inline-block;
	line-height: 1.8;
	text-decoration: none;
	color: inherit;
  }
  
  .footer .menu a:hover {
	color: #0099ff;
  }
  
  .footer .name {
	color: #0099ff;
	font-size: 14px;
	font-weight: normal;
	margin: 0;
  }
  
  @media (max-width: 767px) {
	.footer {
	  font-size: 14px;
	}
	.footer .footer-left,
	.footer .footer-center,
	.footer .footer-right {
	  display: block;
	  width: 100%;
	  margin-bottom: 40px;
	  text-align: center;
	}
	.footer .footer-center i {
	  margin-left: 0;
	}
	footer .copyright {
		padding: 25px 0;
		background: #454444;
		margin-top: 20px;
		font-size: 15px;
	  }
	  
	  
	  
  }
  .copyright {
	padding: 16px ;
	background: #3f3d3d;
	color:white;
	font-size: 20px;
  }